.App__Navigation {
    display: flex;
    justify-content: flex-end;
}

.App__Navigation-sign {
    font-size: 1.5rem;
    border-bottom: 1px solid black;
    cursor: pointer;
    margin-right: 20px;
}

.App__Navigation-sign:hover {
    color: grey;
    border-bottom: 1px solid grey;
}